<template>
  <div>
    <div class="headerTextBd">Search Blood Donation Club</div>
    <div class="blood_group_select mt-2">
      <div class="ml-2 ">
        <label class="mt-2">Select Blood Group</label>
        <b-row>
          <b-col
            id="options"
            v-for="message in bloodGroupOption"
            :key="message.item"
            cols="4"
            xl="1"
            resonsive
          >
            <ul>
              <li
                class="bloodGrpClass"
                @click="selctBloodGroup(message)"
                :class="{ activeBlood: message.item == active }"
              >
                <a>{{ message.name }}</a>
              </li>
            </ul>
            <!-- <b-form-select
            v-model="selectedBloodGroup"
            :options="bloodGroupOption"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
            class="dropDownButton"
          >
          </b-form-select> -->
          </b-col>
        </b-row>
        <b-row>
          <!-- <b-col id="options" cols="12" xl="2" class="justifyMobile">
          <label>Select Country</label>
          <b-form-select
            v-model="selectedCountry"
            :options="CountryOption"
            @change="getStateData"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
            class="dropDownButton"
          >
          </b-form-select>
        </b-col> -->
          <b-col id="options" cols="13" xl="3" class="justifyMobile">
            <label>Select State</label>
            <b-form-select
              v-model="selectedState"
              :options="StateOption"
              @change="getDistData"
              value-field="item"
              text-field="name"
              disabled-field="notEnabled"
              class="dropDownButton"
            >
            </b-form-select>
          </b-col>
          <b-col id="options" cols="12" xl="3" class="justifyMobile">
            <label>Select District</label>
            <b-form-select
              v-model="selectedDist"
              :options="DistOption"
              @change="getCityData"
              value-field="item"
              text-field="name"
              disabled-field="notEnabled"
              class="dropDownButton"
            >
            </b-form-select>
          </b-col>

          <b-col id="options" cols="12" xl="3" class="justifyMobile">
            <label>Select City</label>
            <b-form-select
              v-model="selectedCity"
              :options="CityOption"
              value-field="item"
              text-field="name"
              disabled-field="notEnabled"
              class="dropDownButton"
            >
            </b-form-select>
          </b-col>
          <b-col
            class="btnClass alignButtons justifyMobile btncls mt-2"
            cols="12"
            xl="3"
          >
            <b-button @click="getDonarData" variant="danger"
              ><i class="fa fa-search mr-2" aria-hidden="true"></i>Find
              Donar</b-button
            >
          </b-col>
        </b-row>
        <br />
      </div>
    </div>
    <div class="mt-5">
      <b-row>
        <b-col cols="12" xl="6" class="alignButtons">
          <label class="headerTextBd">DONAR LIST</label>
        </b-col>
        <b-col cols="12" xl="6" class="float-right alignButtons justifyMobile">
          <div class="text-right">
            <b-button @click="showRegisterModal" variant="primary" class=""
              >Register Donar</b-button
            >
            <b-button
              id="fileSelect"
              @click="onPickFile()"
              class="ml-1"
              variant="success"
              >Bulk Upload
            </b-button>
            <input
              id="fileupload"
              ref="fileupload"
              @change="onFileChange"
              type="file"
              name="fileupload"
              hidden
            />
          </div>
        </b-col>
      </b-row>
    </div>
    <div class="mt-4">
      <b-table
        id="adminTable"
        ref="selectableTable"
        :select-mode="selectModeTable"
        :fields="fields"
        :items="DonarData"
        :striped="true"
        :bordered="false"
        :borderless="false"
        :key="indexRefresh"
        head-variant="dark"
        responsive
        sticky-header
      >
        <template v-slot:cell(BloodGroup)="item">{{ item.value }}</template>
        <template v-slot:cell(Name)="item">{{ item.value }}</template>
        <template v-slot:cell(Email)="item">{{ item.value }}</template>
        <template v-slot:cell(Address)="item">{{ item.value }}</template>
        <template v-slot:cell(Action)="item">
          <i
            @click="deleteDetails(item)"
            class="fa fa-trash"
            aria-hidden="true"
          ></i>
          <i
            @click="edit(item)"
            class="fa fa-pencil-square-o ml-1"
            aria-hidden="true"
          ></i>
        </template>
      </b-table>
      <b-modal
        id="tree-modal2"
        ref="tree-modal2"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="lg"
        footer-class="border-1"
        header-class="border-0 pd-0"
        button-size="sm"
      >
        <template v-slot:modal-header="{ cancel }">
          <span class="headerText mt-3 pl-3">Update Blood Donar</span>
          <div class="w0">
            <button
              @click="cancel"
              type="button"
              aria-label="Close"
              class="close close-btn"
            >
              ×
            </button>
          </div>
        </template>
        <div class="line mt-2"></div>
        <b-card class="pop-up-box border-0">
          <b-row>
            <b-col cols="12" xl="4">
              <label>First Name</label>

              <input
                v-model="UpdateInfo.first_name"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.UpdateInfo.first_name"
                :field="$t('first_name')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Last Name</label>
              <input
                v-model="UpdateInfo.last_name"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.UpdateInfo.last_name"
                :field="$t('last_name')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Phone No</label>
              <input
                v-model="UpdateInfo.phone"
                onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                class="form-control"
              />
              <show-errors
                :control="$v.UpdateInfo.phone"
                :field="$t('phone')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4">
              <label>Email</label>
              <input
                v-model="UpdateInfo.email"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.UpdateInfo.email"
                :field="$t('email')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select Blood Group</label>
              <b-form-select
                v-model="UpdateInfo.selected_bloodgroup"
                :options="bloodGroup_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.UpdateInfo.selected_bloodgroup"
                :field="$t('bloodgroup')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Address</label>
              <input v-model="UpdateInfo.address" class="form-control" />
              <show-errors
                :control="$v.UpdateInfo.address"
                :field="$t('address')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select Country</label>
              <b-form-select
                v-model="UpdateInfo.country_id"
                :options="Country_Option"
                @change="getStateDetails(UpdateInfo.country_id)"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.UpdateInfo.selected_Country"
                :field="$t('Country')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select State</label>
              <b-form-select
                v-model="UpdateInfo.state_id"
                :options="State_Option"
                @change="getDistDetails(UpdateInfo.state_id)"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.UpdateInfo.state_id"
                :field="$t('State')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select District</label>
              <b-form-select
                v-model="UpdateInfo.district_id"
                :options="District_Option"
                @change="getCityDetails(UpdateInfo.district_id)"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.UpdateInfo.district_id"
                :field="$t('District')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4">
              <label>Select City</label>
              <b-form-select
                v-model="UpdateInfo.city_id"
                :options="City_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.UpdateInfo.city_id"
                :field="$t('City')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Is Plasma Donar</label>
              <b-form-select
                v-model="UpdateInfo.plasmadonar"
                :options="plasmadonar_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.UpdateInfo.plasmadonar"
                :field="$t('plasmadonar')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>PinCode</label>
              <input
                v-model="UpdateInfo.pincode"
                onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                class="form-control"
              />

              <show-errors
                :control="$v.UpdateInfo.pincode"
                :field="$t('PinCode')"
              />
            </b-col>
          </b-row>
        </b-card>
        <div class="line"></div>
        <b-row>
          <b-col cols="12" class="mb-2 mt-3 d-flex justify-content-end">
            <b-button @click.prevent="close" variant="secondary" class="mr-1"
              >close</b-button
            >
            <b-button @click="UpdateData" variant="primary">Update</b-button>
          </b-col>
        </b-row>
      </b-modal>

      <b-modal
        id="tree-modal1"
        ref="tree-modal1"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="lg"
        footer-class="border-1"
        header-class="border-0 pd-0"
        button-size="sm"
      >
        <template v-slot:modal-header="{ cancel }">
          <span class="headerText mt-3 pl-3">Register Blood Donar</span>
          <div class="w0">
            <button
              @click="cancel"
              type="button"
              aria-label="Close"
              class="close close-btn"
            >
              ×
            </button>
          </div>
        </template>
        <!-- <div class="headerText mt-3">Register Blood Donar</div> -->
        <div class="line mt-2"></div>
        <b-card class="pop-up-box border-0">
          <b-row class="">
            <b-col cols="12" xl="4">
              <label>First Name</label>
              <input
                v-model="RegisterInfo.first_name"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.first_name"
                :field="$t('first_name')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Last Name</label>
              <input
                v-model="RegisterInfo.last_name"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.last_name"
                :field="$t('last_name')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Phone No</label>
              <input
                v-model="RegisterInfo.phone"
                onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.phone"
                :field="$t('phone')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4">
              <label>Email</label>
              <input
                v-model="RegisterInfo.email"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.email"
                :field="$t('email')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select Blood Group</label>
              <b-form-select
                v-model="RegisterInfo.selected_bloodgroup"
                :options="bloodGroup_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_bloodgroup"
                :field="$t('bloodgroup')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Address</label>
              <input v-model="RegisterInfo.address" class="form-control" />
              <show-errors
                :control="$v.RegisterInfo.address"
                :field="$t('address')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select Country</label>
              <b-form-select
                v-model="RegisterInfo.selected_Country"
                :options="Country_Option"
                @change="getStateDetails(RegisterInfo.selected_Country)"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_Country"
                :field="$t('Country')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select State</label>
              <b-form-select
                v-model="RegisterInfo.selected_State"
                :options="State_Option"
                @change="getDistDetails(RegisterInfo.selected_State)"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_State"
                :field="$t('State')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Select District</label>
              <b-form-select
                v-model="RegisterInfo.selected_District"
                :options="District_Option"
                @change="getCityDetails(RegisterInfo.selected_District)"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_District"
                :field="$t('District')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4">
              <label>Select City</label>
              <b-form-select
                v-model="RegisterInfo.selected_City"
                :options="City_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_City"
                :field="$t('City')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>Is Plasma Donar</label>
              <b-form-select
                v-model="RegisterInfo.plasmadonar"
                :options="plasmadonar_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.plasmadonar"
                :field="$t('plasmadonar')"
              />
            </b-col>
            <b-col cols="12" xl="4" class="justifyMobile">
              <label>PinCode</label>
              <input
                v-model="RegisterInfo.pincode"
                onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                class="form-control"
              />

              <show-errors
                :control="$v.RegisterInfo.pincode"
                :field="$t('PinCode')"
              />
            </b-col>
          </b-row>
        </b-card>
        <div class="line"></div>
        <b-row>
          <b-col cols="12" class="mb-2 mt-4 d-flex justify-content-end">
            <b-button @click="close" variant="secondary" class="mr-1"
              >close</b-button
            >
            <b-button @click="RegisterData" variant="primary">Add</b-button>
          </b-col>
        </b-row>
      </b-modal>
    </div>
  </div>
</template>

<script>
import { Settings } from "luxon";
import { required, email, numeric, minLength } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
Settings.defaultLocale = "ja";
import axios from "axios";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");

export default {
  name: "Index",
  components: { ShowErrors },
  data() {
    return {
      indexRefresh: 1,
      selectModeTable: "single",
      selectedBloodGroup: "",
      selectedCountry: "2",
      selectedState: "",
      selectedDist: "",
      selectedCity: "",
      active: "",
      bloodGroupOption: [],
      CountryOption: [],
      StateOption: [],
      DistOption: [],
      CityOption: [],
      DonarData: [],
      tableData: [],
      countryData: [],
      bloodGroupData: [],
      bloodGroup_Option: [],
      Country_Option: [],
      State_Option: [],
      District_Option: [],
      City_Option: [],
      plasmadonar_Option: [
        { item: false, name: "No" },
        { item: true, name: "Yes" },
      ],
      RegisterInfo: {
        first_name: "",
        last_name: "",
        phone: "",
        email: "",
        selected_bloodgroup: "",
        selected_Country: "",
        selected_State: "",
        selected_District: "",
        selected_City: "",
        plasmadonar: "",
        address: "",
        pincode: "",
      },
      UpdateInfo: {
        first_name: "",
        last_name: "",
        phone: "",
        email: "",
        selected_bloodgroup: "",
        selected_Country: "",
        selected_State: "",
        selected_District: "",
        selected_City: "",
        plasmadonar: "",
        address: "",
        address_book_id: "",
        pincode: "",
        city_id: "",
        country_id: "",
        district_id: "",
        state_id: "",
      },
    };
  },
  computed: {
    fields() {
      return [
        {
          key: "BloodGroup",
          label: "BloodGroup",
          // class: "text-center normal-width",
        },
        {
          key: "Name",
          label: "Name",
          // class: "text-center normal-width",
        },
        {
          key: "Email",
          label: "Email",
          // class: "text-center normal-width",
        },
        {
          key: "address",
          label: "Address",
          // class: "text-center normal-width",
        },
        {
          key: "Action",
          label: "Action",
          // class: "text-center normal-width",
        },
      ];
    },
  },
  validations: {
    RegisterInfo: {
      first_name: { required },
      last_name: { required },
      phone: { required, phoneMinLength: minLength(10) },
      email: { required, email },
      selected_bloodgroup: { required },
      selected_Country: { required },
      selected_State: { required },
      selected_District: { required },
      selected_City: { required },
      plasmadonar: { required },
      address: { required },
      pincode: { required },
    },
    UpdateInfo: {
      first_name: { required },
      last_name: { required },
      phone: { required, phoneMinLength: minLength(10) },
      email: { required, email },
      selected_bloodgroup: { required },
      selected_Country: { required },
      selected_State: { required },
      selected_District: { required },
      selected_City: { required },
      plasmadonar: { required },
      address: { required },
      pincode: { required },
    },
  },
  mounted() {
    this.serviceGetFunction();
    this.getBloodGroupData();
    this.getCountryData();
    // this.getStateDetails(this.selectedCountry);
    this.getStateData(this.selectedCountry);
  },
  created() {},
  methods: {
    selctBloodGroup(item) {
      this.active = item.item;
      console.log(item);
    },
    onPickFile() {
      //this.$refs.fileInput.click()
      document.getElementById("fileupload").click();
    },
    async onFileChange(event) {
      console.log(event);
      let fileCSV = event.target.files[0];
      var ext = event.target.files[0].name.split(".").pop();

      if (ext != "csv") {
        this.$swal({
          type: "warning",
          text: "Please Select Csv File",
          time: 4,
        });
        this.onPickFile();
      }
      let url = "master/bulkupload/address_books";
      let payload = {
        column_array: { address: 7, city_id: 8, pincode: 9 },
        dependent_table: "users",
        dep_column_array: {
          first_name: 1,
          last_name: 2,
          email: 3,
          phone: 4,
          is_plasma_donor: 5,
          blood_group_id: 6,
          address_book_id: "foreign_column",
        },
      };
      const formData = new FormData();
      formData.append("attachment", fileCSV);
      formData.append("payload", JSON.stringify(payload));
      // formData.append('name', this.name)
      await BasicInfoApis.postAPI(url, formData, {}).then((res) => {
        if (res.success) {
          this.$swal({
            type: "success",
            text: "uploaded successfuly",
            time: 4,
          });
          this.serviceGetFunction();
        } else {
          this.$swal({
            type: "warning",
            text: " Csv File upload failed",
            time: 4,
          });
        }
      });

      // await BasicInfoApis.postAPI(url, {
      //   column_array: { address: 6, city_id: 7, pincode: 8 },
      //   dependent_table: "users",
      //   dep_column_array: {
      //     first_name: 0,
      //     last_name: 1,
      //     email: 2,
      //     phone: 3,
      //     is_plasma_donor: 4,
      //     blood_group_id: 5,
      //     address_book_id: "foreign_column",
      //   },
      // });
    },
    async deleteDetails(item) {
      console.log("delete item", item);
      let url = `master/delete/users`;
      let Data = await BasicInfoApis.postAPI(url, {
        key: "id",
        value: item.item.id,
      });
      if (Data.data.success) {
        this.$swal({
          type: "success",
          text: "Successfully Deleted Record",
          time: 4,
        });
        this.serviceGetFunction();
      }
    },
    async UpdateData() {
      this.$v.UpdateInfo.$touch();
      if (!this.$v.UpdateInfo.$invalid) {
        let payLoad = [
          {
            table_name: "AddressBook",
            conditions: [
              {
                key: "id",
